.btn-red {
    display: inline-flex;
    height: 5rem;
    border-radius: .4rem;
    background-color: $red;
    padding: 0 4rem;
    color: $white;
    font-family: $font;
    font-size: 1.6rem;
    font-weight: 500;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;
    align-items: center;
    @include link;
    border: 0;
    box-shadow: none;
    cursor: pointer;

    &:hover {
        background: darken($red, 20%);
        color: $white;
    }
}

.btn-link {
    color: $txt;
    font-size: 1.4rem;
    font-weight: 600;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    display: inline-block;
    position: relative;
    cursor: pointer;
    @include link;
    background: none;
    border: 0;

    &:hover {
        &:before {
            transform: scaleX(1);
            transform-origin: bottom left;
        }
        color: $black;
    }

    &:after {
        @include rel-block;
        margin-top: .6rem;
        width: 100%;
        height: .2rem;
        content: '';
        background: $yellow;
    }

    &:before {
        @include abs-block;
        left: 0;
        bottom: 0;
        width: 100%;
        height: .2rem;
        content: '';
        background: $txt;
        z-index: 1;
        transform: scaleX(0);
        transform-origin: bottom right;
        transition: transform 0.25s ease-out;
    }

    svg {
        margin-left: 1rem;
    }
}