html {
    font-size: 62.5%;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -webkit-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    text-size-adjust: 100%;
}

@font-face {
    font-display: swap;
}

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700&display=swap');



body {
    font-weight: 400;
    font-size: 1.6rem;
    font-family: 'Montserrat', sans-serif;
}




$font: 'Montserrat',
sans-serif;
$font1: 'Ivy Mode';

input,
select,
option,
textarea,
label {
    font-weight: 400;
    font-family: $font
}

button {
    font-family: $font;
}

h1,
h2,
h3,
h4,
h5 {
    color: $txt;

    span {
        font-size: 1em;
        color: $black;
    }

    @include mobile {
        br {
            display: none;
        }
    }
}

.typo1 {
    color: $txt;
    font-family: $font;
    font-size: 7rem;
    font-weight: 500;
    font-style: normal;
    letter-spacing: normal;
    line-height: 120%;


    @media (max-width: 992px) {
        font-size: 3rem;

        br {
            display: none;
        }
    }
}

a {
    cursor: pointer !important;
}

p,
li,
span {
    color: $txt;
    font-family: Montserrat;
    font-size: 1.4rem;
    font-weight: 500;
    font-style: normal;
    letter-spacing: normal;
    line-height: 140%;
    text-align: left;

    a {
        color: $txt;
        text-decoration: none;

        &:hover {
            color: $yellow;
        }
    }

    @media (max-width: 767px) {}
}

p {
    margin-bottom: 2rem;

    &:last-child {
        margin-bottom: 0;
    }
}

.main-default {

    p,
    li {
        a {
            color: $black;
            text-decoration: none;

            &:hover {
                color: $red;
            }
        }
    }
}

@media (max-width: 992px) {

    h1,
    h2,
    h3,
    h4,
    p,
    span,
    li,
    a {
        // br {
        //     display: none;
        // }
    }

    .has-text-align-center {
        br {
            display: block
        }
    }
}

.p-large {
    max-width: 42rem;
    color: $txt;
    font-size: 3rem;
    font-weight: 500;
    font-style: normal;
    letter-spacing: normal;
    line-height: 120%;
    text-align: left;

    @include mobile {
        font-size: 2.4rem;
        margin-bottom: 2rem;
    }

    p {
        color: $txt;
        font-size: 3rem;
        font-weight: 500;
        font-style: normal;
        letter-spacing: normal;
        line-height: 120%;
        text-align: left;

        @include mobile {
            font-size: 2.4rem;
        }
    }
}