.btn-link:hover {
  color: #000000;
}
.btn-link:hover:before {
  transform: scaleX(1);
  transform-origin: bottom left;
}
.btn-link:after {
  position: relative;
  display: block;
  margin-top: 0.6rem;
  width: 100%;
  height: 0.2rem;
  content: "";
  background: #fecc0c;
}
.btn-link:before {
  position: absolute;
  display: block;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0.2rem;
  content: "";
  background: #4f4f51;
  z-index: 1;
  transform: scaleX(0);
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.btn-link svg {
  margin-left: 1rem;
}

img {
  vertical-align: middle;
}

