.hero-txt {
    @include rel-block;
    margin-bottom: 10rem;
    padding-top: 6rem;
    z-index: 2;

    @media only screen and (max-width: 992px) {
        padding-top: 0;
        margin-bottom: 5rem;
    }

    .subtitle {
        color: $red;
        font-family: $font;
        font-size: 1.8rem;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 120%;
        margin-bottom: 2rem;
        text-transform: uppercase;
        width: 100%;
        text-align: center;

        @media only screen and (max-width: 992px) {
            font-size: 1.6rem;
        }
    }

    h1 {
        color: $black;
        font-family: $font;
        font-size: 4.6rem;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 130%;
        text-align: center;

        @media only screen and (max-width: 992px) {
            font-size: 3rem;
            line-height: 120%;
        }
    }
}

.hero-realizations {
    @include rel-block;
    margin-bottom: 10rem;
    padding-top: 6rem;
    z-index: 2;

    @media only screen and (max-width: 992px) {
        padding-top: 0;
        margin-bottom: 5rem;
    }

    .subtitle {
        color: $red;
        font-family: $font;
        font-size: 1.8rem;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 120%;
        margin-bottom: 2rem;
        text-transform: uppercase;
        width: 100%;
        text-align: left;

        @media only screen and (max-width: 992px) {
            font-size: 1.6rem;
            text-align: center;
        }
    }

    h1 {
        color: $black;
        font-family: $font;
        font-size: 4.6rem;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 130%;
        text-align: left;

        @media only screen and (max-width: 992px) {
            font-size: 3rem;
            line-height: 120%;
            text-align: center;
        }
    }

    .row {
        .txt {
            flex: 1;
        }

        .button {
            flex: 0 0 24rem;
            align-items: flex-end;

            @media only screen and (max-width: 992px) {
                flex: 0 0 100%;
                justify-content: center;
                padding-top: 2rem;
            }

        }
    }
}

.hero-img {
    @include rel-block;
    z-index: 2;

    .txt {
        flex: 1;
        padding-left: 9rem;
        padding-top: 6rem;
        width: 40.5%;
        z-index: 2;

        @media only screen and (max-width: 992px) {
            width: 100%;
            padding-top: 5rem;
            padding-left: 0;
            order: 2;
        }

        div {
            @media only screen and (max-width: 992px) {
                text-align: center;
            }
        }

        .subtitle {
            color: $red;
            font-family: $font;
            font-size: 1.8rem;
            font-weight: 700;
            font-style: normal;
            letter-spacing: normal;
            line-height: 120%;
            margin-bottom: 2rem;
            text-transform: uppercase;
            max-width: 44rem;
            text-align: left;

            @media only screen and (max-width: 992px) {
                font-size: 1.6rem;
                text-align: center;
                max-width: 100%;
                margin-bottom: 1rem;
            }
        }

        h1 {
            white-space: nowrap;
            max-width: 90rem;

            @media only screen and (max-width: 992px) {
                text-align: center;
                max-width: 100%;
            }

            span {
                color: $black;
                font-family: $font;
                font-size: 4.6rem;
                font-weight: 700;
                font-style: normal;
                letter-spacing: normal;
                line-height: 145%;
                background-color: $white;
                color: $black;
                box-shadow: 0px 0 0 0px #fff, 0px 0 0 0px #fff, 30px 0px 0px 0px #fff;

                @media only screen and (max-width: 992px) {
                    font-size: 3rem;
                    background-color: transparent;
                    white-space: normal;
                    box-shadow: none;

                    br {
                        display: none
                    }
                }
            }
        }

        a {
            margin-top: 4rem
        }
    }

    .image {
        width: 59.5%;

        img {
            border-radius: 1rem
        }

        @media only screen and (max-width: 992px) {
            width: 100%;
            order: 1
        }
    }

    .author {
        display: flex;
        margin-top: 7.4rem;
        flex-wrap: wrap;
        max-width: 80%;
        align-items: center;

        @media only screen and (max-width: 767px) {
            flex: 1 1 auto;
            justify-content: center;
        }

        .avatar {

            @media only screen and (min-width: 768px) {
            flex: 0 0 7.9rem;
        }

            img {
                border-radius: 50%;
            }
        }

        @media only screen and (max-width: 992px) {
            max-width: 100%;
            margin-top: 3rem;
        }

        .name {
            
            color: $black;
            font-family: $font;
            font-weight: 700;
            font-style: normal;
            letter-spacing: normal;
            line-height: 130%;
            text-align: center;

            @media only screen and (min-width: 768px) {
                flex: 1;
            }

            @media only screen and (max-width: 767px) {
                text-align: left;
                padding-left: 1rem;
            }

            span {
                @include rel-block;
                font-weight: 400;
                margin-bottom: .5rem;
            }
        }
    }
}

body.single-post {
    .hero-img .txt h1 {
        width: 69rem;
        white-space: normal;

        @media only screen and (max-width: 992px) {
            width: 100%;
            white-space: normal;
        }
    }

    .hero-img .txt {
        padding-top: 10rem;

        @media only screen and (max-width: 992px) {
            padding-top: 3rem;
        }
    }
}