#footer {
    @include rel-block;
    padding: 10rem 0;

    @include mobile {
        padding: 5rem 0 3rem 0;
    }

    // FOOTER COL
    .footer-col {
        display: flex;
        flex-wrap: wrap;
        width: 100%;

        @include mobile {
            display: block;
        }

        &-item {
            width: 24.3%;

            @include mobile {
                width: 100%;
            }

            a {
                display: inline-block;
                transition: color 0.25s ease-out;

                &:after {
                    display: block;
                    content: '';
                    position: relative;
                    width: 100%;
                    max-width: 22rem;
                    height: .1rem;
                    margin-top: 0rem;
                    transform: scaleX(0);
                    background-color: #242424;
                    transform-origin: bottom right;
                    transition: transform 0.25s ease-out;
                }

                &:hover {
                    color: $black;

                    &:after {
                        transform: scaleX(1);
                        transform-origin: bottom left;
                    }
                }

                @include mobile {
                    &:last-child {
                        margin-bottom: 3rem;
                    }
                }
            }

            p {
                margin-bottom: 1rem;
            }
        }

        .logo {
            width: 17%;
            @include mobile {
                width: 100%;
                margin-bottom: 2rem;
            }
        }

        .social {
            width: 10%;
            background: $white;

            @include mobile {
                width: 100%;
                margin-top: 3rem;
            }
        }
    }

    // SOCIAL
    .footer-social {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        justify-content: flex-end;
        background: $white;

        @include mobile {
            justify-content: flex-start;
        }

        li {
            margin-right: 3.4rem;

            &:last-child {
                margin-right: 0;
            }

            a {
                @include rel-block;
                @include link;
                margin-bottom: 0;

                &:after {
                    display: none;
                }

                &:hover {
                    transform: translate(0px, -.3rem);
                }
            }
        }
    }

    // MENU
    .menu {

        @include mobile {
            margin-top: 1.9rem;
        }
        li {
            margin-bottom: 1.9rem;

            &:last-child {
                margin-bottom: 0;
            }

            a {
                color: $txt;
                font-size: 2.2rem;
                font-weight: 500;
                font-style: normal;
                letter-spacing: normal;
                line-height: 120%;
                transition: color 0.25s ease-out;
                display: inline-block;

                @include mobile {
                    font-size: 1.6rem;
                    margin-bottom: 0 !important;
                }

                &:after {
                    display: block;
                    content: '';
                    position: relative;
                    width: 100%;
                    max-width: 22rem;
                    height: .2rem;
                    margin-top: .2rem;
                    transform: scaleX(0);
                    background-color: #242424;
                    transform-origin: bottom right;
                    transition: transform 0.25s ease-out;
                }

                &:hover {
                    color: $black;

                    &:after {
                        transform: scaleX(1);
                        transform-origin: bottom left;
                    }
                }
            }
        }
        .current-menu-item {
            a {
                color: $black;
            }
        }
    }


    // COL

    .footer-copy {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        padding-left: 17%;
        align-items: center;
        padding-top: 6rem;

        @include mobile {
            padding: 0;
            display: block;
        }

        &-col {
            p, span, a {
                color: #aaaaac;
                font-size: 1.2rem;
                font-weight: 500;
            }

            a {
                transition: color 0.25s ease-out;
                &:hover {
                    color: $black;
                }
            }

            &:first-child {
                flex: 0 0 29.1%;

                @include mobile {
                    margin-bottom: 2rem;
                }
            }
            &:last-child {
                flex: 1;
                display: flex;
                justify-content: flex-end;
                span {
                    display: inline-block;
                    margin-right: 1rem;
                }

                @include mobile {
                    margin-top: 2rem;
                    justify-content: flex-start;
                }
            }
        }
    }
}