.navbar__fixed {
    position: fixed;
    display: block;
    width: 100%;
    left: 0;
    z-index: 100;
    background: $white;
    transition: .15s;
    top: 0;
    border-bottom: .1rem solid $grey;

    @include mobile {
        background: rgba(255,255,255,.9);
        padding: 1rem 0;
    }

    &-cnt {
        display: flex;
        position: relative;
        height: 100%;
        width: 120rem;
        margin: 0 auto;
        align-items: center;
        padding-top: 3.6rem;
        padding-bottom: 3.6rem;

        @media only screen and (min-width: 1200px) {
            width: 120rem !important;
        }

        @media only screen and (min-width: 1400px) {
            width: 136.4rem !important;
        }

        @media only screen and (min-width: 1600px) {
            // padding-top: 6.4rem;
        }

        @media only screen and (max-width: 1199px) {
            width: 100%;
            padding: 0 5%;
        }
    }

    /* LOGO */
    &-logo {
        display: block;
        position: relative;
        max-width: 100%;
        a {
            @include rel-block;
            width: 24.7rem;

            @include mobile {
                width: 100%;
            }
        }
    }

    &-main {
        // width: 100%;
        flex: 1;
        height: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }

    // LANG
    &-lang {
        margin-right: 5rem;

        @include mobile {
            margin-right: 3rem;
        }

        ul {
            display: flex;

            li {
                &:first-child {
                    margin-right: 2rem;
                }

                a {
                    font-size: 1.4rem;
                    font-weight: 500;
                    @include rel-block;

                    &:after {
                        content: '';
                        position: absolute;
                        width: 100%;
                        transform: scaleX(0);
                        height: .2rem;
                        bottom: 0;
                        left: 0;
                        background-color: $yellow;
                        transform-origin: bottom right;
                        transition: transform 0.25s ease-out;
                    }

                    &:hover {
                        color: $black;

                        &:after {
                            transform: scaleX(1);
                            transform-origin: bottom left;
                        }
                    }
                }
            }

            .current-lang {
                a {
                    &:after {
                        transform: scaleX(1);
                        transform-origin: bottom left;
                    }
                }
            }
        }
    }


    /*NAV */
    nav {
        display: block;
        position: relative;
        // @include link;
        flex: 0 0 100%;
        height: 100%;
        color: $white;

        @media (max-width: 1199px) {
            display: none;
        }

        ul {
            display: flex;
            width: 100%;
            height: 100%;
            flex-flow: wrap;
            margin: 0;
            padding: 0;
            // @include link;

            li {
                display: flex;
                position: relative;
                height: 100%;
                align-items: center;
                margin: 0 2.5rem;

                &:first-child {
                    margin-left: 0;
                }

                @media (min-width: 1600rem) {
                    margin: 0 2rem
                }

                a {
                    color: $black;
                    font-family: $font;
                    font-size: 1.6rem;
                    font-weight: 500;
                    font-style: normal;
                    @include link;

                    &:after {
                        content: '';
                        position: absolute;
                        width: 100%;
                        transform: scaleX(0);
                        height: .1rem;
                        bottom: 0;
                        left: 0;
                        background-color: $white;
                        transform-origin: bottom right;
                        transition: transform 0.25s ease-out;
                    }

                    &:hover {
                        color: $red;

                        &:after {
                            transform: scaleX(1);
                            transform-origin: bottom left;
                        }
                    }

                    &:focus,
                    &:visited,
                    &:unvisited {
                        color: $white;
                    }
                }

                // SUBMENU
                ul {
                    display: none;
                    position: absolute;
                    top: 70%;
                    left: -2rem;
                    z-index: 99999;
                    width: 22rem;
                    height: auto;
                    background: $white;
                    padding: 1rem 2rem;
                    @include link;
                    box-shadow: 2px 2px 3px #f4f4f4;

                    li {
                        margin: 0;
                        background: $white;
                        display: block;
                        height: auto;
                        padding: 0;

                        a {
                            color: $black !important;
                            text-align: left;
                            padding: .6rem 0;
                            display: block;
                            position: relative;
                            border-bottom: .1rem solid #f4f4f4 !important;
                            @include link;
                            font-size: 1.5rem !important;
                            line-height: 130%;

                            &:hover {
                                color: $red;
                            }

                            &:after,
                            &:before {
                                display: none !important;
                            }
                        }

                        &:last-child {
                            a {
                                border: 0;
                            }
                        }
                    }

                    .current_page_item,
                    .current-menu-item {
                        a {
                            color: $red !important;
                        }
                    }
                }

                &:hover {
                    ul {
                        display: block
                    }
                }
            }

            .current-menu-item,
            .current-page-ancestor,
            .current_page_parent {
                a {
                    color: $red;
                }
            }
        }
    }

}


.navbar__fixed--small {
    background: rgba(255,255,255,.9);
    // height: 8rem;
    top: -7.6rem;

    .navbar__fixed-logo {
        display: flex;
        align-items: center;
    }
}

.navbar__fixed--up {
top: 0 !important;
}